.card {
    margin-top: 15px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    &:hover {
      transform: scale(1.001);
      box-shadow: 0 6px 12px 0 #39603D;
    }
    .cardImg {
      height: 250px;
    }
    .projectTitle {
      color: #39603D;
      font-family: 'Montserrat', sans-serif;
    }
    .blurb {
        font-family: 'Open Sans', sans-serif;
        max-height: 150px;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .viewProject {
        font-family: 'Montserrat', sans-serif;
        color: #39603D;
        font-weight: 700;
    }
}

  .projectDialog {
    .dialogContentWrapper {
        .content {
            padding: 8px 0;
            overflow: hidden;
        }
        .stickyBack {
            width: 100%;
            height: 64px;
            background-color: white;
            position: fixed;
            top: 0;
            .backWrapper {
                text-align: right;
                padding-right: 15px;
            }
            .closeDialogIcon {
                background-color: white;
                font-family: 'Montserrat', sans-serif;
                font-size: 20px;
                margin-top: 10px;
                &:hover {
                    color: #39603D;
                }
            }
        }
        .projectIntro {
            padding: 0 64px 100px 64px;
            h1 {
                font-size: 4rem;
                @media (max-width: 601px) {
                    padding-top: 64px;
                    font-size: 2rem;
                }
                margin-bottom: 0;
            }
            h3 {
                color: #39603D;
            }
            p, h3 {
                line-height: 1.8;
            }
            p {
                font-style: italic;
            }
        }
        .info {
            background-color: whitesmoke;
            padding: 15px 30px;
            @media (min-width: 600px) {
                padding: 35px 64px;
            }
            &:nth-child(odd) {
                @media (min-width: 1280px) {
                    .projectImg {
                        order: 1;
                    }
                    .caption {
                        order: 2;
                    }
                }
            }
            .caption {
                @media (min-width: 600px) {
                    padding: 60px;
                }
                align-self: center;
                line-height: 1.8;
            }
            .projectImg {
                border-radius: 1.5rem;
                width: 100%;
                height: auto;
                box-shadow: 0px 11px 25px rgb(57 96 61 / 70%);
            }
        }
        .videoContainer {
            text-align: center;
            padding: 60px 0 20px 0;
            .videoGridItem {
                padding: 35px 0 45px 0;
                .video {
                    @media (max-width: 601px) {
                        width: 90%;
                    }
                    border-radius: 15px;
                    box-shadow: 0px 11px 25px rgb(57 96 61 / 70%);
                }
            }
        }  
    }
  }