.app {
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #DADED4;
  .header {
    background-color: #DADED4;
    color: #39603D;
    padding: 0 30px;
    .menuIcon {
      padding: 10px 20px;
      position: absolute;
      top: 25px;
      right: 5px;
      z-index: 100;
      color: black;
    }
    .navbarContent {
      text-transform: uppercase;
      font-size: 1.1rem;
      letter-spacing: 2px;
      color: #39603D;
      ul {
        padding-left: 0px;
        list-style-type: none;
        li {
          display: inline;
          padding-left: 4rem;
          &:first-of-type {
            padding-left: 0px;
          }
          a {
            padding: 4px;
            color: #39603D;
            text-decoration: none;
            transition: border 0.25s;
            border: 3px solid rgba(57,96,61,0);
          }
          a:hover {
            border: 3px solid rgba(57,96,61,1);
          }
        }
      }
    } 
  }
  .intro {
    height: 100vh;
    background-color: #DADED4;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-image: url(./assets/img/monstera.jpg);
    background-size: cover;
    background-position: top;
    .introContent {
      width: 100%;
      padding-bottom: 20px;
      background-color: rgba(0, 0, 0, 0.50);
    }
    h1 {
      font-size: 3.75rem;
      color: white;
      @media (max-width: 960px) {
        font-size: 2rem;
        padding-top: 3rem;
      }
    }
    h3 {
      color: white;
    }
    .hireMe {
      text-transform: uppercase;
      font-size: 1rem;
      letter-spacing: 2px;
      color: white;
      padding: 0 10px;
      a {
        color: white;
      }
    }
  }
  .competencies {
    background-color: rgba(57, 96, 61, 0.1);
    color: #444444;
    padding: 40px 0 65px;
    svg {
      padding-top: 25px;
      color: #39603D;
    }
    h3 {
      color: #39603D;
      font-size: 22px;
    }
    ul, li {
      list-style-type: none;
      padding-inline-start: 0px;
    }
  }
  .portfolio {
    background-color: white;
    color: #3C403D;
    padding: 40px 0 65px;
    .cardContainer {
      @media (max-width: 600px) {
        padding: 0 10px;
      }
    }
  }
  .about {
    background-color: #3C403D;
    color: white;
    padding: 40px 15% 65px;
    text-align: left;
    h2 {
      text-align: center;
    }
  }
}

.closeDialogIcon {
  position: absolute !important;
  top: 10px;
  right: 10px;
}

.menuContent {
  width: 100%;
  ul {
    padding-left: 0px;
    list-style-type: none;
    li {
      font-size: 24px;
      padding: 5rem 5rem;
      a {
        text-decoration: none;
        color: #39603D;
      }
    }
  }
}